.about-us {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10rem;
}

.about-us h1 {
  font-size: 4.6rem;
  color: #1a4264;
  margin-bottom: 2.5rem;
}

.about-us p {
  font-size: 1.8rem;
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

.about-us img {
  border-radius: 0.5rem;
  overflow: hidden;
}

@media screen and (max-width: 1000px) {
  .about-us {
    flex-direction: column;
  }

  .about-us h1 {
    text-align: center;
    margin-bottom: 5rem;
  }

  .about-us p {
    font-size: 2.2rem;
  }

  .about-us .img {
    order: -1;
  }
}
